.homesecWrap {
    .article1 {
        position: relative; z-index: 1; margin-top: 85px;
        .slick-arrow { position: absolute; top: 50%; transform: translateY(-50%);
            background: none; border: none; padding: 10px; z-index: 9; width: 4rem; height: 4rem; 
            color: #fff; cursor: pointer;
            &.slick-prev { left: 10px; }
            &.slick-next { right: 10px; }
        }
        .slick-dots { position: absolute; bottom: 30px; left: 50%; transform: translateX(-50%); 
            li { display: inline-block; 
                &+li { padding-left: 10px; }
                button { width: 15px; height: 15px; border-radius: 50%; background: #fff; 
                    text-indent: -9999px; overflow: hidden; border: none; }
                &.slick-active { button { background: #f64646; } }
            }
        }
        .slide { position: relative;
            height: 590px;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            .text {
                position: absolute; left: 8%;
                top: 50%; transform: translateY(-50%);
                color: #333;
                letter-spacing: -3px;
                p { font-size: 1.8rem;
                    em { font-weight: bold; text-decoration: underline; }
                }
                h3 {font-size: 3.9rem; padding: 1.2rem 0 1.5rem; font-weight: normal;
                    em {font-weight: bold;}
                }
                .hashtag { font-size: 1rem;
                    line-height: 1rem; color: #666;
                    letter-spacing: -1px;
                    margin-bottom: 30px;
                    span{background: #fff;
                    border: 1px solid #a1a1a1;
                    border-radius: 50px; padding: 7px 17px; margin-right: 7px;
                    }
                }
                button {font-size: 1.2rem; background: #f64646; border: none; color: #fff; 
                    padding: 12px 25px; border-radius: 5px;
                    &:hover { background: #bf132b; }
                }
                
            }
        &.slide1 { background-image: url(images/main_visual01.jpg); }
        &.slide2 { background-image: url(images/main_visual02.jpg); }
        &.slide3 { background-image: url(images/main_visual03.jpg); 
            .text { p {color: #fff;} h3 {color: #fff;} .hashtag { span {border:none;} } }
        }
        }
    }
    .article2 {
        .row {
            .at1Wrap { display: flex; flex-wrap: wrap; justify-content: space-between; padding: 60px 0;
                color: #333;
                .box { width: 23%; margin-left: 30px;
                    span { color: #666; }
                }
                .box1 { margin-left: 0;
                    h3 { font-size: 2rem; position: relative; padding-bottom: 10px; margin-bottom: 13px;
                        font-weight: 800;
                        &:before { content: ''; display: block; position: absolute;
                            width: 40px; height: 2px; background: #333; bottom: -4px; }
                    }
                    .tagbox { padding-top: 16px;
                        p {font-size: 1.1rem; color: #666; border: 1px solid #c1c1c1; cursor: pointer;
                            display: inline-block; padding: 5px 20px; border-radius: 50px; margin-bottom: 5px;
                            &:hover { background: #f64646; color: #fff; border: 1px solid transparent;

                            }
                        }
                    }
                }
                .box2 { background: #ffed93; text-align: center; padding: 35px 20px; cursor: pointer;
                    em { font-size: 1.3rem; font-weight: 700; }
                    h3 { font-size: 1.6rem; font-weight: 800; padding: 4px 0 20px;}
                    .img {
                        display: inline-block; width: 219px; height: 219px; max-width: 100%;
                        background: url('./images/main_at1_1.png') no-repeat left top;
                        background-size: contain;
                    }
                    p { font-size: 1.3rem; font-weight: 800; padding: 10px 0 4px; }
                }
                .box3 { background: #f5eee5; text-align: center; padding: 35px 0; cursor: pointer;
                    em { font-size: 1.3rem; font-weight: 700; }
                    h3 { font-size: 1.6rem; font-weight: 800; padding: 4px 0 13px;}
                    span { font-weight: 700; }
                    .img {
                        display: block; max-width: 100%; height: 150px;
                        background: url('./images/main_at1_2.jpg') no-repeat left top;
                        background-size: cover; margin: 20px 0;
                    }
                    p { font-size: 1.3rem; font-weight: 800; padding-top: 6px; }
                }
                .box4 { background: url('./images/main_at1_3.jpg') no-repeat left top;
                    background-size: 100% 100%; padding: 35px 25px; color: #fff;
                    h3 { font-size: 1.8rem; font-weight: 800; padding-bottom: 20px; }
                    span { color: #fff; }
                }
            }
        }
    }
    .article3 { background: url(./images/main_at2_bg.jpg) no-repeat center;
        height: 405px;
        .txtBox { text-align: center; padding-top: 65px; color: #333;
            p { font-size: 1.6rem; font-weight: 700; margin-bottom: 14px;
                strong { font-size: 2rem; color: #f64646; }
                em { font-size: 2.4rem; font-weight: 800; line-height: 3.4rem; }
            }
            button { font-size: 1.2rem; border: 2px solid #666; letter-spacing: -2px;
                background: transparent; border-radius: 50px; padding: 6px 23px;
                &:hover { background: #333; border: 2px solid transparent; color: #fff; }
            }
        }
    }
    // 타이틀박스 공통
    .titBox { text-align: center; padding-bottom: 40px;
        h2 { position: relative; font-size: 2.4rem; padding-bottom: 22px; margin-bottom: 18px;
            &:before {
                content: ''; position: absolute; display: inline-block; width: 40px; height: 2px;
                background: #333; bottom: 0px; left: 50%; transform: translateX(-50%);
            }
        }
        p { font-size: 1.1rem; color: #666; }
    }
    .article4 { color: #333; padding: 50px 0 80px;
        .recommWrap { display: flex; height: 800px;
            span { color: #666; }
            h3 { font-size: 1.4rem; color: #333; margin-top: 5px; }
            .left { display: flex; flex-direction: column; width: 50%; margin-right: 40px;
                .top { background: #ffed93; height: 66%; margin-bottom: 30px; cursor: pointer;
                    .img { background: url(./images/main_recomm_left_1.jpg) no-repeat center; 
                        background-size: 100%; height: 76%; transition: all 0.5s;
                        &:hover { background-size: 105%; }
                    }
                    .txt { padding: 30px 0 30px 20px;
                        h3 { font-size: 1.8rem; }
                    }
                }
                .bottom { display: flex; height: 34%;
                    .b_left { width: 50%; margin-right: 20px; cursor: pointer; position: relative;
                        .img { background: url(./images/main_recomm_left_2.jpg) no-repeat center; 
                            background-size: 100%; height: 74%; transition: all 0.5s;
                            &:hover { background-size: 105%; }
                        }
                        .txt { position: absolute; bottom: 0; }
                    }
                    .b_right { width: 50%; cursor: pointer; position: relative;
                        .img { background: url(./images/main_recomm_left_3.jpg) no-repeat center; 
                            background-size: 100%; height: 74%; transition: all 0.5s;
                            &:hover { background-size: 105%; }
                        }
                        .txt { position: absolute; bottom: 0; }
                    }
                }
            }
            .right { display: flex; flex-direction: column; width: 50%;
                .top { display: flex; height: 34%; margin-bottom: 30px;
                    .t_left { width: 50%; margin-right: 20px; cursor: pointer; position: relative;
                        .img { background: url(./images/main_recomm_right_1.jpg) no-repeat center; 
                            background-size: 100%; height: 74%; transition: all 0.5s;
                            &:hover { background-size: 105%; }
                        }
                        .txt { position: absolute; bottom: 0; }
                    }
                    .t_right { width: 50%; cursor: pointer; position: relative;
                        .img { background: url(./images/main_recomm_right_2.jpg) no-repeat center; 
                            background-size: 100%; height: 74%; transition: all 0.5s;
                            &:hover { background-size: 105%; }
                        }
                        .txt { position: absolute; bottom: 0; }
                    }
                }
                .bottom { background: #ffed93; height: 66%; bottom: 0; cursor: pointer;
                    .img { background: url(./images/main_recomm_right_3.jpg) no-repeat center; 
                        background-size: 100%; height: 76%; transition: all 0.5s;
                        &:hover { background-size: 105%; }
                    }
                    .txt { padding: 30px 0 30px 20px;
                        h3 { font-size: 1.8rem; }
                    }
                }
            }
        }
    }
    .article5 { padding: 60px 0 50px;
        background: url(./images/main_tip_bg.jpg) no-repeat center; background-size: cover;
        .row {
            .contWrap { text-align: center;
                ul { display: flex; justify-content: space-between; padding-bottom: 40px;
                    li { width: 33.33%; padding: 0 30px;
                        .img { width: 100%; height: 300px; display: inline-block;
                            background-repeat: no-repeat; background-position: center;
                        }
                        &:nth-child(1) { .img { background-image: url(./images/main_tip_1.jpg); } }
                        &:nth-child(2) { .img { background-image: url(./images/main_tip_2.jpg); } }
                        &:nth-child(3) { .img { background-image: url(./images/main_tip_3.jpg); } }
                        p { color: #666; padding: 22px 0 5px; }
                        h3 { color: #333; font-size: 1.6rem; }
                    }
                }
                button { font-size: 1.1rem; font-weight: 700; padding: 7px 40px; background: transparent;
                    border: 2px solid #333; border-radius: 50px; color: #333;
                    &:hover { background: #333; color: #fff; }
                }
            }
        }
    }
}