.headerWrap {position: fixed; top: 0; left: 0; width: 100%; color: #fff; background: linear-gradient(to right, #bf132b, #f64646); transition: all 0.5s; z-index: 9;
    .row {position: relative; display: flex; justify-content: space-between; align-items: center; width: 1400px; height: 85px; margin: 0 auto;
        .logo { 
            .lnk {
                display: block;
                width: 240px; height: 66px;
                background: url('./images/logo.png') no-repeat left top;
            }
        }
        .nav {
            position: absolute;
            left: 50%; transform: translate(-50%);
            .depth1 {display: flex; 
                li {position: relative; line-height: 85px; cursor: pointer; margin: 0 30px;
                    font-size: 1.1rem; font-weight: 700;
                    .depth2 { display: none;
                        position: absolute; top: 98%; left: 50%; transform: translate(-50%);
                        width: 150px; background: #fff; padding: 15px 0; color: #333;
                        box-shadow: 0 6px 12px rgba(0, 0, 0, .175);
                        &.on, &.on2, &.on3 {display: block;}
                        li {line-height: 2.5rem; margin: 0; text-align: center; font-size: 1rem;
                            transition: all 0.2s;
                            &:hover {background: #eee;}
                        }
                    }
                }
            }
        }
        .mobileNav {position: relative;
            button { padding: 10px 20px; border-radius: 50px; border: none; font-size: 1.3rem; display: flex; color: #333; background: #fff; cursor: pointer;
                .ham {margin-right: 10px;}

            }
            .mobileNavList { display: none;
                position: absolute; top: 100%; right: 0; margin-top: 10px;
                background: #fff; color: #333; padding: 10px 0; width: 100%; border-radius: 10px;
                &.opener { display: block; }
                ul {
                    li {line-height: 1.5rem; padding: 0 1rem;}
                    li:last-child {border-top: 1px solid #ddd; padding-top: 10px; margin-top: 10px;}
                }
            }
        }
    }
}